/* eslint-disable no-sequences */
/* eslint-disable no-unused-vars */
/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-expressions */

import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { defaultReject, defaultState } from "../../../constants";
import { fetchData } from "../../../helpers";

const LOGINCREATE = createAsyncThunk(
  "LoginCreate/LoginCreate",
  // eslint-disable-next-line default-param-last
  async (
    // eslint-disable-next-line default-param-last
    payload = {},
    { rejectWithValue }
  ) => {
    try {
      const data = await fetchData(
        payload?.data,
        payload?.method,
        payload?.apiName
      );
      return {
        ...defaultState.List,
        message: data?.data.Message,
        data: data?.data?.data,
      };
    } catch (error) {
      return rejectWithValue({
        ...defaultReject.List,
        message: error.message,
      });
    }
  }
);

const LoginCreateSlice = createSlice({
  name: "LoginCreateSlice",
  initialState: {
    LoginCreate: {
      ...defaultState.List,
      loading: false, 
      error: false, 
    },
  },
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(LOGINCREATE.fulfilled, (state, action) => {
      state.LoginCreate = {
        ...state.LoginCreate,
        loading: false,
        error: false,
        ...action.payload,
      };
    });
    builder.addCase(LOGINCREATE.pending, (state, action) => {
      state.LoginCreate = {
        ...state.LoginCreate,
        loading: true,
        error: false,
        ...action.payload,
      };
    });
    builder.addCase(LOGINCREATE.rejected, (state, action) => {
      state.LoginCreate = {
        ...state.LoginCreate,
        loading: false,
        error: true,
        ...action.payload,
      };
    });
  },
}); 

const LoginCreateAction = {
  LOGINCREATE,
};

export { LoginCreateAction };
export default LoginCreateSlice.reducer;
